@import '../../assets/variables.scss';

.form__wrapper {
    border: 1px solid $light-grey;
    border-radius: 5px;
    margin: 1rem 2rem 1rem 1rem;
    padding: 1rem;
}

.form__title {
    font-family: $heading-font;
    font-size: 1.4rem;
    margin-top: .5rem;
    margin-bottom: 1rem;
}

.errMess {
    color: red;
    font-weight: bold;
    font-size: 0.9rem;
}

.message {
    margin-top: 1rem;
    font-size: .8rem;
    font-weight: bold;
    &_success {
        color: green;
    }
    &_error {
        color: red;
    }
    &_loading {
        margin-top: 1rem;
        color: #007bff;
        opacity: 1;
        font-size: 1.5rem;
    }
}

.submit_box {
    display: flex;
    align-items: center;
    gap: 1rem;
}